import { useState } from "react";
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { useNavigate } from "react-router-dom";
import Appbar from "./components/appbar";
import { addRegularReserve, logDataRegular } from "../data/firebase";

export default function RegularReservePage () {
  var navigate = useNavigate();

  var [name, setName] = useState('');
  var [week, setWeek] = useState('');
  var [time, setTime] = useState('');
  var [pw, setPw] = useState('');
  var [rePw, setRePw] = useState('');
  var [errMessage, setErrMessage] = useState('');

  async function clickReserveButton () {

    if (name.trim() === '') {
      setErrMessage(' ');
    }

    else if (week === '') {
      setErrMessage(' ');
    }

    else if (time === '') {
      setErrMessage(' ');
    }

    else if (pw === '') {
      setErrMessage(' ');
    }

    else if (pw !== rePw) {
      setErrMessage(' ');
    }

    else {
      setErrMessage('');
      addRegularReserve({ name: name, week: week, time: time, pw: pw, timeStamp: Date.now().toString() });
      logDataRegular({ name: name, week: week, time: time, pw: pw, timeStamp: Date.now().toString() });
      alert('');
      navigate('/');
    }
  }


  return (
    <div className="flex flex-col items-center justify-start w-screen min-h-screen p-2">
      <Appbar title=" " />

      <div className="mb-9 mt-10">

        <TextField
          id="outlined-basic"
          label=" "
          variant="outlined"
          size="small"
          sx={{ width: 190 }}
          onChange={(e) => { setName(e.target.value); }}
          value={name}
        />
      </div>



      <div className="flex flex-col items-center justify-center">
        <div>{ }</div>
        <FormControl size="small" sx={{ width: 190 }}>
          <InputLabel id="demo-simple-select-label"></InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={week}
            label=""
            onChange={(e: SelectChangeEvent) => { setWeek(e.target.value); }}
          >
            <MenuItem value="tue"></MenuItem>
            <MenuItem value="thu"></MenuItem>
          </Select>
        </FormControl>

        <div className="h-10" />

        <FormControl size="small" sx={{ width: 190 }}>
          <InputLabel id="demo-simple-select-label"></InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={time}
            label=""
            onChange={(e: SelectChangeEvent) => { setTime(e.target.value); }}
          >
            <MenuItem value="reg_time1">17:30 ~ 18:30</MenuItem>
            <MenuItem value="reg_time2">18:00 ~ 19:00</MenuItem>
            <MenuItem value="reg_time3">18:30 ~ 19:30</MenuItem>
          </Select>

        </FormControl>
      </div>

      <div className="h-10" />

      <div>
        <TextField
          id="outlined-basic"
          label=""
          variant="outlined"
          size="small"
          type="password"
          sx={{ width: 190 }}
          onChange={(e) => setPw(e.target.value)}
          value={pw}
        />
      </div>

      <div className="h-10" />

      <div>
        <TextField
          id="outlined-basic"
          label=" "
          variant="outlined"
          size="small"
          type="password"
          sx={{ width: 190 }}
          onChange={(e) => setRePw(e.target.value)}
          value={rePw}
        />
      </div>

      <div className="h-5" />

      <div className="text-basic">      .</div>

      <div className="h-10" />

      <div className="text-red-500">{errMessage}</div>

      <div className="h-6" />

      <a
        href="#!"
        className="flex flex-col items-center justify-center w-32 h-9 bg-button rounded-xl"
        onClick={clickReserveButton}
      >
        <div></div>
      </a>

      <div className="h-6" />


    </div>
  );
}